import wei from '@/static/mbcImg/my/15.png';
import xuan from '@/static/mbcImg/my/16.png';
import lineRightArrow from '@/static/mbcImg/images/common/line-right-arrow.png';
export default {
	
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			wei:wei,
			xuan:xuan,
			lineRightArrow:lineRightArrow,
			date: currentDate,
			tou:false,
			images:0,
			imag:1,
		}
	},
	mounted() {
		
	},
	created() {
		
	},
	
	created() {
	},
	methods: {
		image(){
			this.images=1;
			this.imag=0;
		},
		ima(){
			this.images=0;
			this.imag=1;
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
		
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		bindDateChange: function(e) {
			// console.log(e)
			this.date = e.target.value
			console.log(this.date)
		},
		baoadd(){
			if(this.images==1){
				console.log('选择永久')
				this.$store.commit('setEffective','永久');
			}else if(this.imag==1){
				console.log('选择短期')
				this.$store.commit('setEffective',this.date);
			}
			uni.navigateBack({})
		},
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
	},
}
